.card {
    margin-top: 40px;
    &_row {
        display: flex;
        align-items: flex-start;
        .xs-block({
            flex-direction: column;
            align-items: center;
        });
    }
    &_photo {
        width: 330px;
        .xxs-block({
            width: 100%;
        });
    }
    &_item {
        border-radius: 10px;
        img {
            object-fit: cover;
            border-radius: 10px;
            margin: auto;
        }
    }
    &_dop {
        border-left: 2px solid #F7F7F7;
        margin-left: 35px;
        padding-left: 35px;
        .md-block({
            padding-left: 0;
            margin-left: 0;
            border-left: 0;
            margin-bottom: 25px;
        });
        &--p {
            font-size: 14px;
            color: #000;
        }
        &--ul {
            margin-top: 10px;
        }
        &--li {
            margin: 0 5px;
            display: inline-block; 
        }
        &--input {
            display: none;
            &:checked+label {
                background: #FFB100;
                color: #fff;
            }
        }
        &--label {
            display: inline-block;
            cursor: pointer;
            width: 50px;
            height: 25px;
            background: #F7F7F7;
            border-radius: 5px;
            text-align: center;
            line-height: 25px;
            font-size: 14px;
            color: #A5A5A5;
            transition: .4s all ease;
        }
        &--calc {
            margin-top: 22px;
        }
        &--parent {
            display:flex;
            justify-content:center;
            width: 150px;
            height: 32px;
            border-radius: 5px 5px 0px 0px;
        }
        &--child {
            height: 32px;
            background-color: #FAFAFA;
            border-radius: 5px 5px 0px 0px;
            width: 50%;
            cursor: pointer;
            transition: .4s all ease;
            line-height: 32px;
            font-size: 14px;
            color: #A5A5A5;
            text-align: center;
            &.active { 
                border: 2px solid #FFB100;
                border-bottom: none;
                border-radius: 5px 5px 0px 0px;
                color: #FFB100;
                background-color: #fff;
            }
        }
        &--body {
            width: 284px;
            height: 158px;
            border: 2px solid #FFB100;
            border-radius: 0px 5px 5px 5px;
            position: relative;
            padding: 18px 26px 18px 16px;
            &::after {
                content: '';
                width: 71px;
                height: 2px;
                position: absolute;
                top: -2px; left: 0;
                background-color: #fff;
            }
        }
        &--line {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
            padding-bottom: 10px;
            border-bottom: 1px solid #E5E5E5;
            &:last-child {
                border-bottom: none;
            }
        }
        &--p {
            font-size: 14px;
            line-height: 120%;
            color: #000;
        }
        &--calcin {
            display: flex;
            align-items: center;
            width: 120px;
            height: 20px;
            margin-left: 20px;
            .numb {
                width: 50px;
                height: 20px;
                background: #FFFFFF;
                box-shadow: inset 0px 0px 4.25806px rgba(0, 0, 0, 0.15);
                font-size: 14px;
                color: #FFB100;
                text-align: center;
            }
            .plus {
                background: #FFB100;
                border-radius: 3px 3px 0px 0px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
            .minus {
                background: #FFB100;
                border-radius: 0px 0px 3px 3px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
        }
        &--red {
            margin-left: 10px;
            font-size: 20px;
            line-height: 120%;
            color:  #FFB100;
            .m();
        }
    }
    &__slider {
        width: 330px;
        height: 300px;
        border-radius: 10px;
    }
    &__nav {
        margin-top: 15px;
        .slick-track {
            display: flex;
            align-items: center;
        }
        &--item {
            width: 50px;
            height: 50px;
            border-radius: 5px;
            cursor: pointer;
            margin: 0 8px;
            img {
                object-fit: cover;
                border-radius: 3px;
            }
        }
    }
    &_text {
        margin-left: 30px;
        .xs-block({
            margin-left: 0;
            margin-top: 20px;
        });
        &--title {
            font-size: 20px;
            color: #000;
        }
        &--row {
            display: flex;
            align-items: flex-start;
            margin-top: 20px;
            &2 {
                .lg-block({
                    flex-wrap: wrap;
                });
            }
            .md-block({
                flex-direction: column;
            });
        }
        &--gray {
            .md-block({
                order: 2;
                margin-top: 25px;
            });
            .xs-block({
                order: 1;
                margin-top: 0;
            });
            li {
                color: #A5A5A5;
                font-size: 14px;
                margin: 10px 0;
                &:first-child {
                    margin-top: 0;
                }
                &.last {
                    color: #009900;
                }    
            }
        }
        &--block {
            border-left: 2px solid #F7F7F7;
            margin-left: 35px;
            padding-left: 35px;
            max-width: 475px;
            &2 {
                border-left: none;
                margin-left: 0;
                padding-left: 0;
                max-width: 275px;
                margin-left: 66px;
                .card_text--trig {
                    width: 100%;
                }
                .card_text--red {
                    margin-left: 0;
                }
            }
            .md-block({
                border-left: none;
                margin-left: 0;
                padding-left: 0;
            });
            .xs-block({
                order: 2;
            });
        }
        &--old {
            text-decoration: line-through;
            color: #E5E5E5;
            font-size: 14px;
        }
        &--gray {
            color: #A5A5A5;
            font-size: 14px;
        }
        &--line {
            display: flex;
            align-items: center;
            margin-bottom: 15px;
        }
        &--p {
            font-size: 14px;
            line-height: 120%;
            color: #000;
        }
        &--calc {
            display: flex;
            align-items: center;
            width: 120px;
            height: 20px;
            margin-left: 88px;
            .numb {
                width: 50px;
                height: 20px;
                background: #FFFFFF;
                box-shadow: inset 0px 0px 4.25806px rgba(0, 0, 0, 0.15);
                font-size: 14px;
                color: #FFB100;
                text-align: center;
            }
            .plus {
                background: #FFB100;
                border-radius: 3px 3px 0px 0px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
            .minus {
                background: #FFB100;
                border-radius: 0px 0px 3px 3px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
        }
        &--red {
            margin-left: 10px;
            font-size: 20px;
            line-height: 120%;
            color: #FF0F00;
            .m();
        }
        &--flex {
            margin-top: 5px;
            display: flex;
            align-items: center;
        }
        &--btn {
            .btn();
            width: 150px;
            height: 40px;
            line-height: 38px;
            margin-right: 15px;
            cursor: pointer;
        }
        &--bottom {
            border-top: 1px solid #F7F7F7;
            margin-top: 15px;
            padding-top: 6px;
            display: flex;
            flex-wrap: wrap;
        }
        &--trig {
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            width: 50%;
            .xs-block({
                width: 100%;
            });
            &__p {
                margin-left: 14px;
                font-size: 14px;
                color: #000;
            }
        }
    }
}

.characteristic {
    margin-top: 80px;
    &_row {
        .tabs {
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom: 4px solid #E5E5E5;
            .xxs-block({
                overflow: scroll;
            });
        }
        .tab {
            font-size: 18px;
            line-height: 210%;
            color: #A5A5A5;
            margin: 0 30px;
            cursor: pointer;
            .xs-block({
                margin: 0 15px;
            });
            &.active {
                color: #009900;
                position: relative;
                &::after {
                    content: '';
                    position: absolute;
                    bottom: -4px; left: 50%;
                    transform: translateX(-50%);
                    height: 4px;
                    width: 100%;
                    background-color: #009900;
                }
            }
        }
        .tab_item {
            p {
                font-size: 14px;
                color: #000;
            }
        }
    }
    &_mob {
        display: none;
        .xxs-block({
            display: block;
            font-size: 18px;
            color: #000;
        });
        &::after {
            content: '\2192';
        }
    }
    &_item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 17px;
        p + p {
            text-align: right;
        }
    }
    &_desc {
        margin-top: 20px;
        font-size: 14px;
        color: #000;
        line-height: 135%;
    }
    &_reviews {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        &--up {
            display: flex;
            align-items: flex-start;
            .xs-block({
                flex-direction: column;
            });
        }
        &--item {
            &:nth-child(n + 2) {
                .xs-block({
                    margin-top: 20px;
                });   
            }
        }
        &--down {
            margin-top: 30px;
        }
        &--top {
            display: flex;
            align-items: center;
        }
        &--name {
            font-size: 14px;
            color: #000;
            .m();
        }
        &--date {
            margin-left: 10px;
            color: #A5A5A5;
            font-size: 14px;
        }
        &--p {
            margin-top: 10px;
            font-size: 14px;
            line-height: 140%;
            max-width: 625px;
            padding-right: 10px;
        }
    }
    &_form {
        &--row {
            display: flex;
            align-items: center;
            
        }
        &--input {
            height: 35px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 0 15px;
            font-size: 14px;
            &::placeholder {
                font-size: 14px;
                color: #C4C4C4;
            }
            &1 {
                width: 230px;
            }
            &2 {
                margin-left: 10px;
                width: 508px;
            }
        }
        &--textarea {
            width: 100%;
            margin-top: 10px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 15px;
            height: 65px;
            font-size: 14px;
            &::placeholder {
                font-size: 14px;
                color: #C4C4C4;
            }
        }
        &--submit {
            display: flex;
            justify-content: flex-end;
        }
        &--btn {
            margin: 20px 0 0 auto;
            .btn();
            width: 146px;
            height: 35px;
            line-height: 33px;
            border-radius: 5px;
            font-size: 14px;
            cursor: pointer;
        }
    }
}

@media screen and (max-width:1300px) {
    .characteristic_form--input {
        width: 32.5%;
        margin: 0;
    }
    .characteristic_reviews--down {
        width: 100%;
    }
    .characteristic_form--row {
        justify-content: space-between;
    }
}


.zakazano {
    margin-top: 40px;
    .md-block({
        margin-top: 25px;
    });
    &_row {
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        border-top: 1px solid #E5E5E5;
        padding-top: 28px;
        .lg-block({
            flex-direction: column;
            align-items: center;
        });
        .md-block({
            margin-top: 25px;
            padding-top: 20px;
        });
    }
    &_left {
        .xxs-block({
           width: 100%;
        });
        &--top {
            display: flex;
            align-items: center;
            .xs-block({
                flex-direction: column;
                align-items: flex-start;
            });
        }
        &--label {
            display: flex;
            align-items: center;
            &2 {
                margin-left: 40px;
                .xs-block({
                   margin: 10px 0 0;
                });
            }
            input {
                display: none;
                &:checked + i::after {
                    opacity: 1;
                }
            }
            p {
                margin-left: 10px;
                font-size: 14px;
                color: #000;
            }
        }
        &--radio {
            width: 19px;
            height: 19px;
            border: 2px solid #E5E5E5;
            border-radius: 100%;
            display: inline-block;
            position: relative;
            .xs-block({
                width: 30px;
                height: 30px;
            });
            &::after {
                content: '';
                position: absolute;
                top: 50%; left: 50%;
                transform: translate(-50%,-50%);
                width: 7px;
                height: 7px;
                background: #009900;
                border-radius: 100%;
                opacity: 0;
                transition: .4s all ease;
            }
        }
        &--form {
            display: flex;
            flex-direction: column;
            .md-block({
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: space-between;
            });
            .xs-block({
                flex-direction: column;
            });
        }
        &--input {
            width: 400px;
            height: 40px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 0 15px;
            margin-top: 20px;
            &::placeholder {
                font-size: 13px;
                color: #C4C4C4;
            }
            .md-block({
                width: 345px;
            });
            .xs-block({
                width: 100%;
            });
        }
        &--textarea {
            width: 400px;
            height: 83px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 15px;
            margin-top: 20px;
            &::placeholder {
                font-size: 13px;
                color: #C4C4C4;
            }
            .md-block({
                width: 100%;
            });
        }
        &--bottom {
            margin-top: 25px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .md-block({
                justify-content: flex-start;
            });
            .xxs-block({
                flex-direction: column;
            });
        }
        &--price {
            font-size: 14px;
            span {
                display: block;
                font-size: 20px;
                .m();
            }
            .xxs-block({
                order: 2;
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                margin-top: 15px;
            });
        }
        &--btn {
            .btn();
            width: 230px;
            height: 40px;
            line-height: 38px;
            .md-block({
               margin-left: 25px;
            });
            .xxs-block({
                order: 1;
                width: 100%;
                margin-left: 0;
            });
        }
    }
    &_right {
        .lg-block({
            margin-top: 25px;
        });
        &--item {
            display: flex;
            align-items: center;
            padding: 15px 45px 15px 0;
            border-bottom: 1px solid #E5E5E5;
            position: relative;
            .xs-block({
                flex-wrap: wrap;
                padding-right: 0;
            });
        }
        &--img {
            width: 100px;
            height: 100px;
            object-fit: cover;
            border-radius: 5px;
            .xs-block({
               width: 70px;
               height: 70px;
            });
        }
        &--p {
            margin-left: 40px;
            font-size: 14px;
            .xs-block({
                margin-left: 10px;
                max-width: 140px;
            });
        }
        &--price {
            font-size: 14px;
            margin-left: 116px;
            .m();
            .xs-block({
                margin-left: 25px;
            });
        }
        &--calc {
            display: flex;
            align-items: center;
            width: 120px;
            height: 40px;
            margin-left: 30px;
            .xs-block({
                margin: 10px 0 0;
                width: 100%;
            });
            .numb {
                width: 50px;
                height: 40px;
                background: #FFFFFF;
                box-shadow: inset 0px 0px 4.25806px rgba(0, 0, 0, 0.15);
                font-size: 14px;
                color: #FFB100;
                text-align: center;
                .xs-block({
                    width: 132px;
                });
            }
            .plus {
                background: #FFB100;
                border-radius: 3px 3px 0px 0px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
                height: 40px;
                line-height: 40px;
            }
            .minus {
                background: #FFB100;
                border-radius: 0px 0px 3px 3px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
                height: 40px;
                line-height: 40px;
            }
        }
        &--close {
            position: absolute;
            top: 10px; right: 0;
            cursor: pointer;
            svg path {
                transition: .4s all ease;
            }
            &:hover {
                svg path {
                    fill:#ff5252;
                }
            }
        }
        &--bottom {
            margin-top: 25px;
            height: 50px;
            background: #F7F7F7;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding-right: 30px;
        }
        &--p {
            font-size: 14px;
        }
        &--itog {
            font-size: 14px;
            .m();
            margin-left: 90px;
        }
    }
}