.top {
    background: #FFFFFF;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.07);
    height: 100%;
    &_row {
        height: 75px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .md-block({
            flex-direction: column;
            align-items: flex-start;
            height: auto;
            padding: 25px 0;
        });
    }
}

.breadcrumbs {
    ul {
        display: flex;
        .xs-block({
            flex-wrap: wrap;
        });
    }
    li {
        position: relative;
        padding-right: 15px;
        &::after {
            content: '';
            position: absolute;
            top: 5.5px; right: 0;
            background: url(../img/breadcrumbs_after.svg);
            width: 5px;
            height: 8px;
        }
        &:not(:first-child) {
            margin-left: 15px;
        }
        &:last-child {
            padding-right: 0;
            &::after {
                display: none;
            }
        }
        & > a {
            font-size: 14px;
            color: #A5A5A5;
            &:hover {
                text-decoration: underline;
            }
        }   
    }
    &_active {
        font-size: 14px;
        color: #000000;
        position: relative;
        top: 3px;
        .xs-block({
            margin: 10px 0 0 0 !important;
        });
    }
}

.filter {
    display: flex;
    align-items: center;
    .md-block({
        margin-top: 25px;
    });
    .xxs-block({
        justify-content: space-between;
        width: 100%;
    });
    &_button {
        cursor: pointer;
        align-items: center;
        justify-content: center;
        width: 126px;
        height: 35px;
        background: #009900;
        border-radius: 5px;
        display: none;
        .md-block({
            display: flex;
        });
        p {
            color: #fff;
            font-size: 14px;
            line-height: 1;
            margin-left: 10px;
        }
    }
    &_div {
        display: flex;
        align-items: center;
        select {
            appearance:none;
            outline:0;
            box-shadow:none;
            border:0!important;
            background: #fff;
            background-image: none;
            flex: 1;
            color: #000;
            padding: 0 15px;
            cursor:pointer;
        }
        select::-ms-expand {
            display: none;
        }
        .select {
            position: relative;
            display: flex;
            width: 190px;
            height: 35px;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            overflow: hidden;
            margin-left: 30px;
            &2 {
                width: 250px;
                .xxs-block({
                    width: 164px;
                    margin-left: 0;
                });
            }
        }
        .select::after {
            content: '';
            position: absolute;
            top: 12px;
            right: 12px;
            background: url(../img/filterselect.svg);
            width: 16px;
            height: 9px;
            cursor:pointer;
            pointer-events:none;
        }
    }
    &_toogle {
        width: 86px;
        height: 35px;
        border: 2px solid #E5E5E5;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-left: 30px;
    }
    &_btn {
        width: 50%;
        height: 100%;
        transition: .4s all;
        cursor: pointer;
        &.active {
            height: 105%;
            position: relative;
            left: -2px;
            background: #FFB100;
            border-radius: 5px;
            svg circle {
                fill: #fff;
            }
        }
        svg circle {
            transition: .4s all;
        }
    }
}

.catalog {
    background-color: #FAFAFA;
    padding: 33px 0 100px;
    &_row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    &_left {
        width: 287px;
        .md-block({
            display: none;
        });
    }
    &_right {
        .md-block({
            width: 100%;
        });
        &--row {
            display: flex;
            justify-content: flex-end;
            flex-wrap: wrap;
            align-items: flex-start;
            margin: 0 -11px;
            .md-block({
               justify-content: center;
            });
        }
    }
    &_card {
        width: 303px;
        .xs-block({
            width: 248px;
            margin: 0 auto 22px;
            box-shadow: none;
            border: 1px solid #c4c4c4;
        });
        .xxs-block({
            width: 85%;
        });
        background: #FFFFFF;
        box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
        border-radius: 10px;
        padding: 20px;
        border: 2px solid #fff;
        transition: .4s all;
        position: relative;
        margin: 0 11px 22px;
        .a {
            position: absolute;
            z-index: 4;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        &:hover {
            border: 2px solid #525252;
        }
    }
}

.navbar {
    width: 287px;
    &_item {
        padding-bottom: 20px;
        border-bottom: 1px solid #E5E5E5;
        &:not(first-child) {
            margin-top: 18px;
        }
        &:last-child {
            border-bottom: none;
            padding-bottom: 0;
        }
    }
    &_top {
        display: flex;
        align-items: center;
        cursor: pointer;
        &.active {
            img {
                transition: .4s all ease;
                position: relative;
                transform: rotate(90deg);
            }
        }
        &--p {
            margin-left: 10px;
            font-size: 18px;
            color: #000000;
        }
    }
    &_content {
        padding-top: 17px;
    }
    &_btns {
        margin-top: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &_apply {
        width: 150px;
        height: 40px;
        background: #FFB100;
        border: 2px solid #FFB100;
        border-radius: 5px;
        line-height: 36px;
        cursor: pointer;
        transition: .4s all ease;
        .m();
        font-size: 14px;
        color: #FFF;
        &:hover {
            background-color: #fff;
            color: #FFB100;
        }
    }
    &_off {
        width: 120px;
        height: 40px;
        background: #fff;
        border: 2px solid #A5A5A5;
        border-radius: 5px;
        line-height: 36px;
        cursor: pointer;
        transition: .4s all ease;
        .m();
        font-size: 14px;
        color: #A5A5A5;
        &:hover {
            color: #fff;
            background-color: #A5A5A5;
        }
    }
}

.range {
    &__div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
    &__label {
        position: relative;
        cursor: pointer;
    }
    &__input {
        width: 130px;
        height: 35px;
        background: #FFFFFF;
        border: 2px solid #E5E5E5;
        border-radius: 5px;
        padding: 0 13px;
        cursor: pointer;
        font-size: 14px;
        line-height: 14px;
        color: #A5A5A5;
    }
    &__slider-price {
        width: 96%;
        margin: 20px 5px 0;
    }
}

.range {
    &__div2 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
    &__label2 {
        position: relative;
        cursor: pointer;
    }
    &__input2 {
        width: 111px;
        height: 35px;
        background: #FFFFFF;
        border: 2px solid #E5E5E5;
        border-radius: 5px;
        padding: 0 13px;
        cursor: pointer;
        font-size: 14px;
        line-height: 14px;
        color: #A5A5A5;
    }
    &__slider-price2 {
        width: 96%;
        margin: 28px 5px 10px;
    }
}

.check {
    &_div {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &_label {
        cursor: pointer;
        input {
            display: none;
            &:checked + i {
                border: 2px solid #FFB100;
                &::before {
                    opacity: 1;
                }
            }
        }
        i {
            display: inline-block;
            position: relative;
            width: 17px;
            height: 17px;
            background: #FFFFFF;
            border: 2px solid #E5E5E5;
            border-radius: 3px;
            transition: .4s all ease;
            &::before {
                content: '';
                position: absolute;
                top: 0; left: 0;
                width: 100%;
                height: 100%;
                background: #FFB100;
                transition: .4s all ease;
                opacity: 0;
            }
        }
    }
    &_p {
        position: relative;
        top: -2px;
        margin-left: 10px;
        font-size: 14px;
        color: #A5A5A5;
    }
}

.fmnu {
    top: 0;
    left: 0;
    width: 320px;
    position: fixed;
    height: 100%;
    padding: 0;
    .fancybox-button {
        right: 10px;
        padding: 5px;
        position: fixed;
        top: 2px;
    }
    .fancybox-button svg path {
        fill: #fff;
    }
    &_item {
        border-bottom: 1px solid #E5E5E5;
    }
    &_top {
        padding: 25px 30px;
        display: flex;
        align-items: center;
        &--p {
            margin-left: 10px;
            font-size: 18px;
            color: #000;
        }
    }
    .navbar {
        width: 100%;
        &_item {
            margin-top: 0;
            padding: 25px 30px;
        }
    }
    .noUi-horizontal .noUi-handle {
        width: 30px;
        height: 30px;
        top: -13.5px;
    }
    .check_label i {
        width: 30px;
        height: 30px;
    }
    .navbar_btns {
        flex-direction: column;
        margin-top: 0;
        padding: 25px 30px;
    }
    .navbar_apply,.navbar_off {
        width: 100%;
    }
    .navbar_off {
        margin-top: 20px;
    }
}

@media screen and (max-width:760px) {
    .filter_toogle {
        display: none;
    }
    .filter_div .select.select1 {
        display: none;
    }
}