}

.intro_btn {
  margin-top: 35px;
  background: linear-gradient(180deg, #68DE65 0%, #009900 100%);
  border-radius: 30px;
  font-family: 'RobotoMedium', 'Arial', sans-serif;
  font-size: 18px;
  color: #fff;
  text-align: center;
  line-height: 50px;
  transition: .4s all;
  height: 50px;
  width: 300px;
  display: block;
}

.intro_btn:hover {
  background: linear-gradient(180deg, #68DE65 0%, #68DE65 100%);
}

.intro_btn:active {
  background: #009900;
}

.intro_arrow {
  display: none;
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  justify-content: space-between;
  padding: 0 30px;
}

.intro_dots {
  position: absolute;
  bottom: 35px;
  left: 0;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
}

.intro_dots li {
  position: relative;
  display: inline-block;
  width: 10px;
  height: 10px;
  background: none;
  margin: 0 15px;
  padding: 0;
  cursor: pointer;
  border-radius: 100%;
}

.intro_dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  border-radius: 100%;
}

.intro_dots li button:hover,
.intro_dots li button:focus {
  outline: none;
}

.intro_dots li button:hover:before,
.intro_dots li button:focus:before {
  opacity: 1;
}

.intro_dots li button:before {
  font-size: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  content: '';
  text-align: center;
  background-color: transparent;
  border: 2px solid #9E9E9E;
  border-radius: 100%;
}

.intro_dots li.slick-active button:before {
  border-color: #009900;
  background-color: #009900;
}

.slick-prev:before,
.slick-next:before {
  display: none;
}

.slick-next {
  right: 5px;
}

.slick-prev {
  left: 5px;
}

.slick-dotted.slick-slider {
  margin-bottom: 0;
}

