@media screen and (max-width: 760px) {
  .filter_toogle {
    display: none;
  }

  .filter_div .select.select1 {
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .header_logo {
    display: none;
  }

  .header_search {
    margin-left: 3px;
  }

  .header_search--input {
    width: 191px;
    border-radius: 30px;
  }

  .header_search--btn {
    display: none;
  }

  .header_tels {
    display: none;
  }

  .mobmenu {
    padding: 15px 10px 100px;
  }

  .mobmenu_top {
    flex-direction: column;
  }

  .mobmenu_currency {
    margin-top: 15px;
  }

  .mobmenu1 li {
    margin: 30px 0;
  }

  .mobmenu1 li > a {
    font-size: 16px;
  }

  .intro_block {
    padding: 40px 0 0;
  }

  .intro_p {
    font-size: 14px;
  }

  .intro_btn {
    margin-top: 25px;
    width: 100%;
  }

  .intro_arrow {
    top: 94%;
    padding: 0 5px;
  }

  .trig_item {
    max-width: inherit;
  }

  .menu .tab {
    width: 15%;
    padding: 15px 0;
  }

  .menu .tab.active {
    width: 60%;
  }

  .menu .tab.active .tab_text {
    color: #A5A5A5;
    display: inline-block;
    font-size: 16px;
  }

  .menu .tab_text {
    display: none;
  }

  .menu .tab_head {
    padding: 10px 30px;
  }

  .menu .tab_head--mob {
    display: inline-block;
    font-size: 20px;
    color: #fff;
  }

  .menu .tab_head--mnu {
    display: none;
  }

  .menu .tab_slider .slick-list {
    padding: 0;
  }

  .menu .tab_dots {
    display: flex !important;
    opacity: 1;
  }

  .menu .tab_card {
    width: 248px;
    box-shadow: none;
    border: 1px solid #c4c4c4;
  }

  .reviews_arrow {
    display: none;
  }

  .reviews_arrow2 {
    right: 0%;
  }

  .news {
    margin-top: 100px;
  }

  .news_row {
    flex-direction: column;
  }

  .news_right {
    margin: 20px 0 0;
  }

  .news_item {
    width: 100%;
    height: 320px;
  }

  .news_item3 .news_text--btn {
    display: block;
  }

  .news_item4 {
    display: none;
  }

  .news_text--btn {
    width: 260px;
    font-size: 16px;
  }

  .watched_card {
    width: 248px;
    margin: auto;
    box-shadow: none;
    border: 1px solid #c4c4c4;
  }

  .watched_slider .slick-list {
    padding: 0;
  }

  .watched_arrow {
    display: none;
  }

  .watched_arrow1 {
    left: 0%;
  }

  .watched_arrow2 {
    right: 0%;
  }

  .about_text--title {
    font-size: 26px;
  }

  .about_text--more {
    font-size: 16px;
  }

  .follow_form {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .follow_form--input {
    width: 100%;
    border-radius: 30px;
    border-right-width: 2px;
  }

  .follow_form--btn {
    margin-top: 20px;
    width: 100%;
    border-radius: 30px;
  }

  .footer_col {
    padding-right: 20px;
  }

  .footer_col:not(:first-child) {
    padding-left: 20px;
  }

  .header_search--all {
    display: none;
  }

  .header_search--left {
    border: none;
  }

  .breadcrumbs ul {
    flex-wrap: wrap;
  }

  .breadcrumbs_active {
    margin: 10px 0 0 0 !important;
  }

  .catalog_card {
    width: 248px;
    margin: 0 auto 22px;
    box-shadow: none;
    border: 1px solid #c4c4c4;
  }

  .card_row {
    flex-direction: column;
    align-items: center;
  }

  .card_text {
    margin-left: 0;
    margin-top: 20px;
  }

  .card_text--gray {
    order: 1;
    margin-top: 0;
  }

  .card_text--block {
    order: 2;
  }

  .card_text--trig {
    width: 100%;
  }

  .characteristic_row .tab {
    margin: 0 15px;
  }

  .characteristic_reviews--up {
    flex-direction: column;
  }

  .characteristic_reviews--item:nth-child(n + 2) {
    margin-top: 20px;
  }

  .zakazano_left--top {
    flex-direction: column;
    align-items: flex-start;
  }

  .zakazano_left--label2 {
    margin: 10px 0 0;
  }

  .zakazano_left--radio {
    width: 30px;
    height: 30px;
  }

  .zakazano_left--form {
    flex-direction: column;
  }

  .zakazano_left--input {
    width: 100%;
  }

  .zakazano_right--item {
    flex-wrap: wrap;
    padding-right: 0;
  }

  .zakazano_right--img {
    width: 70px;
    height: 70px;
  }

  .zakazano_right--p {
    margin-left: 10px;
    max-width: 140px;
  }

  .zakazano_right--price {
    margin-left: 25px;
  }

  .zakazano_right--calc {
    margin: 10px 0 0;
    width: 100%;
  }

  .zakazano_right--calc .numb {
    width: 132px;
  }
}

@media screen and (max-width: 544px) {
  .menu .tab_card {
    width: 100%;
  }

  .reviews_item {
    flex-direction: column;
  }

  .reviews_item--text {
    margin: 20px 0 0 0;
  }

  .watched_card {
    width: 100%;
  }

  .footer_row {
    flex-direction: column;
  }

  .footer_col {
    padding: 0;
    padding-bottom: 25px;
    border-bottom: 1px solid #232323;
    border-right: none;
  }

  .footer_col:not(:first-child) {
    padding: 0;
    padding-bottom: 25px;
    margin-top: 25px;
  }

  .footer_col:last-child {
    padding: 0;
    padding-bottom: 25px;
  }

  .footer_bottom {
    margin-top: 25px;
  }

  .filter {
    justify-content: space-between;
    width: 100%;
  }

  .filter_div .select2 {
    width: 164px;
    margin-left: 0;
  }

  .catalog_card {
    width: 85%;
  }

  .card_photo {
    width: 100%;
  }

  .characteristic_row .tabs {
    overflow: scroll;
  }

  .characteristic_mob {
    display: block;
    font-size: 18px;
    color: #000;
  }

  .zakazano_left {
    width: 100%;
  }

  .zakazano_left--bottom {
    flex-direction: column;
  }

  .zakazano_left--price {
    order: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 15px;
  }

  .zakazano_left--btn {
    order: 1;
    width: 100%;
    margin-left: 0;
  }
}